import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Offset from '../../../../Views/Offset';
import Spinner from '../../../../Views/Spinner';
import CardApplicationTopReview from '../../../../Views/CardApplicationTopReview/CardApplicationTopReview';
import Message from '../../../../Views/Messages/Message/Message';
import Pagination from '../../../../Views/Pagination/Pagination';
import { Scrollable, changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';
import {
    SafeAreaView,
    View,
    VirtualizedList,
    StyleSheet,
    Text,
    StatusBar,
  } from 'react-native';

const TopReview = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg } = useStoreon('tg');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });
    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {

        dispatch('setTextHeader', state?.name_button ?? '-100');
        dispatch('setActiveHeaderText', true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);

        tg.BackButton.onClick(() => navigate('/button_rating', {
            state: {
                name_button: state?.old_name_button ?? '  '
            }
        }));

    }, []);

    useEffect(() => {

        const paramsList = {
            url: '/api_top_rating/',
            page: 1,
            page_size: 30,
            dataRequst: (res) => {
                const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
                if (isWarning) return;
                setListAplication(c => ({ ...c, ...res.result }));
            },
        };
        setTimeout(() => {
            dispatch('getListApplications', paramsList)
        }, 100);

    }, []);

    const changePaginationPage = data => {
        const { page } = data;
        const paramsList = {
            url: '/api_top_rating/',
            page: page ?? 1,
            page_size: 30,
            setFieldValue: () => { },
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
                if (isWarning) return;
                const copyData = res?.result?.results.slice();
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...copyData]
                }))
            },
            redirectTo: (res) => { }
        };
        dispatch('getListApplications', paramsList)
    }



    const getItem = (_data, index) => ({
        id: Math.random().toString(12).substring(0),
        title: `Item ${index + 1}`,
      });
      
      const getItemCount = _data => 50;
      
      const Item = ({title}) => (
        <View style={styles.item}>
          <Text style={styles.title}>{title}</Text>
        </View>
      );
      

    return (
        <React.Fragment>
            <Message
                message={messageInForm.textMessage}
                colorMessage={messageInForm.colorMessage}
            />
            {
                listAplication.results.length ?
                    <React.Fragment>
                        {/* <Scrollable>
                            <CardApplicationTopReview
                                listAplication={listAplication}
                                changePaginationPage={changePaginationPage}
                            />
                        </Scrollable> */}
<SafeAreaView style={styles.container}>
      <VirtualizedList
        initialNumToRender={40}
        renderItem={({item}) => <Item title={item.title} />}
        keyExtractor={item => item.id}
        getItemCount={getItemCount}
        getItem={getItem}
      />
    </SafeAreaView>


                        {
                            listAplication.count > 30 ?
                                <Pagination
                                    data={listAplication.results}
                                    allCount={listAplication.count}
                                    count={30}
                                    handlerChangePaginations={changePaginationPage}
                                    currentPage={Number(listAplication.current_page)}
                                    location={'center'}
                                /> : null
                        }
                        <Offset mb={90} />
                    </React.Fragment>
                    : <Spinner
                        text={'  '}
                    />
            }
        </React.Fragment>
    )
}
export default TopReview;

const styles = StyleSheet.create({
    container: {
      flex: 1,
      marginTop: StatusBar.currentHeight,
    },
    item: {
      backgroundColor: '#f9c2ff',
      height: 150,
      justifyContent: 'center',
      marginVertical: 8,
      marginHorizontal: 16,
      padding: 20,
    },
    title: {
      fontSize: 32,
    },
  });